<template>
	<view class="house">
		<watermark></watermark>
		<!-- 顶部导航 -->
		<navTabs :list="list" @getChange="_getChange" @getData="_getData" @clickPrj="_clickPrj"></navTabs>
		<view class="express_box">
			<view class="title">套数</view>
			<view class="express_list_wrap">
				<view class="express_list" v-for="(item,index) in tdList" :key="index">
					<view class="proIndex">{{index + 1}}</view>
					<view class="right_box">
						<view class="proName">{{item.projectName}}</view>
						<view class="bottom">
							<view class="bottom_item">
								<text>总量</text>
								<text>{{item.followUpOverdue}}</text>
							</view>
							<view class="bottom_item">
								<text>去化率</text>
								<text>{{item.signOverdue}}</text>
							</view>
							<view class="bottom_item">
								<text>余量</text>
								<text>{{item.subscribeOverdue}}</text>
							</view>
						</view>
					</view>
				</view>
			</view>
			<utable :thList="thList" :tdList="tdList" :status="2"></utable>
		</view>
		
		<view class="express_box">
			<view class="title">面积</view>
			<view class="express_list_wrap">
				<view class="express_list" v-for="(item,index) in tdList" :key="index">
					<view class="proIndex">{{index + 1}}</view>
					<view class="right_box">
						<view class="proName">{{item.projectName}}</view>
						<view class="bottom">
							<view class="bottom_item">
								<text>总量</text>
								<text>{{item.followUpOverdue}}</text>
							</view>
							<view class="bottom_item">
								<text>去化率</text>
								<text>{{item.signOverdue}}</text>
							</view>
							<view class="bottom_item">
								<text>余量</text>
								<text>{{item.subscribeOverdue}}</text>
							</view>
						</view>
					</view>
				</view>
			</view>
			<utable :thList="thList" :tdList="tdList" :status="2"></utable>
		</view>
		
		<view class="express_box">
			<view class="title">营收</view>
			<view class="express_list_wrap">
				<view class="express_list" v-for="(item,index) in tdList" :key="index">
					<view class="proIndex">{{index + 1}}</view>
					<view class="right_box">
						<view class="proName">{{item.projectName}}</view>
						<view class="bottom">
							<view class="bottom_item">
								<text>总量</text>
								<text>{{item.followUpOverdue}}</text>
							</view>
							<view class="bottom_item">
								<text>去化率</text>
								<text>{{item.signOverdue}}</text>
							</view>
							<view class="bottom_item">
								<text>余量</text>
								<text>{{item.subscribeOverdue}}</text>
							</view>
						</view>
					</view>
				</view>
			</view>
			<utable :thList="thList" :tdList="tdList" :status="2"></utable>
		</view>
		
		
		
		<view class="tabs_item" v-if="current==0">
			<view class="express_box">
				<view class="title">套数</view>
				<view class="express_list_wrap">
					<view class="express_list" v-for="(item,index) in tdList" :key="index">
						<view class="proIndex">{{index + 1}}</view>
						<view class="right_box">
							<view class="proName">{{item.projectName}}</view>
							<view class="bottom">
								<view class="bottom_item">
									<text>总量</text>
									<text>{{item.followUpOverdue}}</text>
								</view>
								<view class="bottom_item">
									<text>去化率</text>
									<text>{{item.signOverdue}}</text>
								</view>
								<view class="bottom_item">
									<text>余量</text>
									<text>{{item.subscribeOverdue}}</text>
								</view>
							</view>
						</view>
					</view>
				</view>
				<utable :thList="thList" :tdList="tdList" :status="2"></utable>
			</view>
		</view>
		<view class="tabs_item" v-if="current==1">
			<view class="express_box">
				<utable :thList="thList" :tdList="tdList" :status="12"></utable>
			</view>
		</view>
		<view class="tabs_item" v-if="current==2">
			<view class="express_box">
				<utable :thList="thList1" :tdList="tdList" :status="15"></utable>
			</view>
		</view>
		<tabBar></tabBar>
	</view>
</template>

<script>
	import watermark from '@/components/watermark.vue';
	import tabBar from '@/components/tabBar.vue';
	import navTabs from '@/components/nav-tabs.vue';
	import barEcharts from '@/components/echartsComponent/barEcharts.vue';
	import utable from '@/components/utable.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				current: 0,
				list: [{
					name: '套数'
				}, {
					name: '面积'
				}, {
					name: '营收'
				}],
				form: {
					companyId:'', // 公司id
					projectId:'', // 项目id
					tabCode: '', // tabs 0金额 1套数 2面积 3均价
				},
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				thList:[{name:'序号'},{name:'项目'},{name:'总量'},{name:'去化率'},{name:'余量'}],
				thList1:[{name:'序号'},{name:'项目'},{name:'总量'},{name:'销售率'},{name:'余量'}],
				tdList:[],
				// tdList:[{id:'1',name:'恒大地产',total:'100',rate:'20%',allowance:'10'},{id:'2',name:'碧桂园',total:'42',rate:'30%',allowance:'3'}],
			}
		},
		methods: {
			// 获取列表
			getList() {
				let that = this
				that.form.tabCode = that.current
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('3008', Object.assign({}, that.form, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.tdList = that.etype == 3 ? that.tdList.concat(res.data.data.tableData.data) : res.data.data.tableData.data
						that.totalPages = res.data.data.tableData.totalPage
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 点击公司回调
			_getData(val, val2){
				this.form.companyId = val
				this.form.projectId = val2
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 点击项目回调
			_clickPrj(val){
				this.form.projectId = val
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this.getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this.flag = true
					this.getList()
				}
			},
			// tabs切换
			_getChange(index) {
				this.current = index
				this.page.pageNum = 1
				this.tdList = []
				this.form.time = ''
				this.getList()
			},
		},
		components: {
			watermark,
			tabBar,
			navTabs,
			barEcharts,
			utable
		},
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
</style>
